import React, { useState, useEffect, useRef } from "react";
import { MenuItems } from "./MenuItems";
import { Link } from "react-scroll";
import { gsap } from 'gsap';
import ScrollTrigger from "gsap/ScrollTrigger";
import "./scss/Navbar.scss";
import {
  FaFacebookF,
  FaTwitter,
  FaGithub,
  FaLinkedinIn,
} from "react-icons/fa";

function Navbar({ mainDiv }) {
  gsap.registerPlugin(ScrollTrigger)

  const [open, setOpen] = useState(false);
  const [sticky, setSticky] = useState(false);

  const updatingState = () => {
    window.addEventListener('scroll', () => {
      if (window.scrollY > 100) {
        setSticky(true)
      } else setSticky(false)
    })
  }
  useEffect(() => {

    // ScrollTrigger.scrollerProxy(mainDiv.current, {
    //   // scrollTop(value) {
    //   //   return arguments.length ?
    //   //     locoScroll.scrollTo(value, 0, 0) :
    //   //     locoScroll.scroll.instance.scroll.y
    //   // },
    //   getBoundingClientRect() {
    //     return { top: 0, left: 0, width: window.innerWidth, height: window.innerHeight }

    //   },
    //   pinType: mainDiv.current.style.transform ? 'transform' : 'fixed'
    // });

    // ScrollTrigger.create({
    //   trigger: navbarRef.current,
    //   scroller: mainDiv.current,
    //   onToggle: setSticky(true),

    // })
    // // ScrollTrigger.addEventListener('refresh', () => locoScroll.update())
    // ScrollTrigger.refresh()
    window.addEventListener("scroll", () => {
      updatingState()
    })


  }, []);






  const navbarRef = useRef()
  const firstLine = useRef();
  const secondLine = useRef();
  const thirdLine = useRef()

  const toggle = () => {
    setOpen(!open);
    //gsap timeline 

  };

  const navbarAnimation = () => {
    if (open) {

      gsap.to(firstLine.current, {
        y: 8,
        rotation: '45deg',
        duration: .1,
        ease: "power2.out"
      })
      gsap.to(secondLine.current, {
        x: 10,
        opacity: 0,
        ease: 'expo.out',
        duration: .1
      }, '<')
      gsap.to(thirdLine.current, {
        y: '-8',
        rotation: '-45deg',
        duration: .1,
        ease: "power2.out"
      })
      // animating the clippath circle for the menu icon
      const navLinksTl = gsap.timeline({ repeat: 0 });
      navLinksTl.to('.nav-links', {
        "clipPath": "circle(1000px at 90% -10%)",
        'pointerEvents': 'all',
        duration: .1,
      })
      // animating the menu links 
      navLinksTl.to('.nav-links li', {
        y: 40,
        duration: .1,
        ease: "back.easeOut.config(4)",
        opacity: 1
      })
    } else {

      // undoing the animation for the clip path circle
      gsap.to('.nav-links', {
        "clipPath": "circle(66px at 90% -10%)",
        'pointerEvents': 'none',
        duration: .1,
      })
      // undoing the animation for the menu links
      gsap.to('.nav-links li', {
        opacity: 0,
        y: 0,
        ease: "back.easeOut.config(4)",
        duration: .1,
        stagger: .1,
      })
      // undoing the animation for the burger menu item

      gsap.to(firstLine.current, {
        y: 0,
        rotation: 0,
        duration: .2
      })
      gsap.to(secondLine.current, {
        x: 0,
        opacity: 1,
        ease: 'expo.out',
        duration: .2
      })
      gsap.to(thirdLine.current, {
        y: 0,
        rotation: 0,
        duration: .2
      })
    }
  }

  useEffect(() => {

    if (window.innerWidth <= 768) {
      navbarAnimation()
    }

  })


  const linkActive = () => {
    setOpen(false)

    if (window.innerWidth <= 768) {
      // undoing the animation for the clip path circle
      gsap.to('.nav-links', {
        "clipPath": "circle(66px at 90% -10%)",
        'pointerEvents': 'none',
        duration: .1,
      })
      // undoing the animation for the menu links
      gsap.to('.nav-links li', {
        opacity: 0,
        y: 0,
        ease: "back.easeOut.config(4)",
        duration: .1,
        stagger: .1,
      })
      // undoing the animation for the burger menu item

      gsap.to(firstLine.current, {
        y: 0,
        rotation: 0,
        duration: .2
      })
      gsap.to(secondLine.current, {
        x: 0,
        opacity: 1,
        ease: 'expo.out',
        duration: .2
      })
      gsap.to(thirdLine.current, {
        y: 0,
        rotation: 0,
        duration: .2
      })
    }

  }

  return (
    <div className="navbar-wrapper">
      <nav className={`${sticky ? 'sticky' : ''}`} ref={navbarRef}>
        <div className="hamburger" onClick={toggle}>
          <div className="line f" ref={firstLine}></div>
          <div className="line s" ref={secondLine}></div>
          <div className="line t" ref={thirdLine}></div>
        </div>
        <div className="social-icons">
          <a href="https://www.facebook.com/mahmoud.essam.1422/">
            <FaFacebookF />
          </a>
          <a href="https://twitter.com/MahmoudEssam69">
            <FaTwitter />
          </a>
          <a href="https://github.com/MahmoudEssam12">
            <FaGithub />
          </a>
          <a href="https://www.linkedin.com/in/mahmoud-essam-956898182/">
            <FaLinkedinIn />
          </a>
        </div>
        <ul className='nav-links'>
          {MenuItems.map((item, index) => (
            <li key={index} >
              <Link to={item.url} activeClass="active" spy={true}
                smooth={true}
                offset={-70}
                onClick={linkActive}
                duration={500}
                className="cnav-link">{item.title}</Link>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
}

export default Navbar;
