import React, { useRef, useEffect, useCallback } from "react";
import { gsap } from "gsap";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import particlesOptions from "./particles-nasa.json";
import "./scss/Home.scss";

function Home() {
  const particlesInit = useCallback(main => {
    loadFull(main);
  }, [])
  const particlesLoaded = (container) => {
    console.log(container);
  };
  const frontend = useRef();
  const backend = useRef();
  const fullStack = useRef();
  const tl = gsap.timeline({ repeat: 200 });



  useEffect(() => {
    tl.from(frontend.current, { y: 25, opacity: 0, duration: 0.5 }).to(frontend.current, { y: -25, opacity: 0, duration: 0.5, delay: 3, display: "block" }).set(frontend.current, { display: "none" });
    tl.from(backend.current, { y: 25, opacity: 0, duration: 0.5, display: "none" }).to(backend.current, { y: -25, opacity: 0, duration: 0.5, delay: 3, display: "block" }).set(backend.current, { display: "none" });
    tl.from(fullStack.current, { y: 25, opacity: 0, duration: 0.5, display: "none" }).to(fullStack.current, { y: -25, opacity: 0, duration: 0.5, delay: 3, display: "block" }).set(fullStack.current, { display: "none" });

  });
  return (
    <div id="home" className="home-header">
      {/* <Particles options={particlesOptions} init={particlesInit} /> */}
      <Particles
        id="tsparticles"
        init={particlesInit}
        loaded={particlesLoaded}
        options={particlesOptions}
        style={{ position: "relative !important" }}
      />
      <div className="heading">
        <span>Welcome to my Portfolio</span>
        <h1>Hi, I'm Mahmoud Essam</h1>
        <h2>
          <div
            className="job"
            style={{ display: "inline-block" }}>
            <p ref={frontend}>
              Front-End&nbsp;
            </p>
            <p ref={backend}>Back-End&nbsp;</p>
            <p ref={fullStack}>FullStack&nbsp;</p>
          </div>
          Web Developer.</h2>
      </div>
    </div>
  );
}

export default Home;
